import * as React from 'react';
import styled from 'styled-components';
import { TableRole as PluginRoles } from '../../../utils/role';
import { Tooltip } from '@ali/we-design';
import { AdaptNormal } from '@ali/we-icon';
import { TableLocale } from '../../types';

const Button = styled.span`
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(17,31,44,0.72);
  margin: 0;
  padding: 0;
  width: 28px;
  height: 28px;
  font-size: 16px;
  cursor: pointer;
  line-height: 16px;
  border-radius: 4px;

  &:hover {
    background-color: rgba(17,31,44,0.08);
  }
`;

interface ToolbarMenuProps {
  style?: React.CSSProperties;
  onMenuEvent: (command: string) => void;
  locale?: TableLocale;
}

export default class TableWidthFitContainerButton extends React.Component<ToolbarMenuProps> {
  handleMouseDown = (e) => {
    e.preventDefault(); // Prevent blur.
    e.stopPropagation();
    const { onMenuEvent } = this.props;
    onMenuEvent('table-width-fit-container');
  };

  render() {
    const { style, locale } = this.props;

    return (
      <Button
        style={style}
        onMouseDown={this.handleMouseDown}
        onTouchStart={this.handleMouseDown}
        data-role={PluginRoles.tableFitWidth}
        data-testid="table-width-fit-button"
      >
        <Tooltip placement="left" title={locale?.widthFitContainer}>
          <span>
            <AdaptNormal
              style={{
                fontSize: '20px',
                color: '#171A1D',
              }}
            />
          </span>
        </Tooltip>
      </Button>
    );
  }
}
